  import userinfomation from '../personalCenter/own-space'
  import { getCountInfo } from '@/api/home';
  import util from '@/libs/util'

  export default {
    components: {
      dataSourcePie,
      inforCard,
      userinfomation

    },
    data () {
      return {
        dataTop5: [
