        count: {
          db: 0,
          order: 0,
          task: 0,
          review: 0
        },
        newToDoItemValue: '',
        time: '',
        username: sessionStorage.getItem('user'),
      }
    },
    methods: {
      formatDate () {
        let date = new Date();
        let month = date.getMonth() + 1;
        this.time = date.getFullYear() + '/' + month + '/' + date.getDate() + '  ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds()
      }
    },
    mounted () {
      getCountInfo().then(res => {
        console.log(res.data.data)
        this.count.task = res.data.data.task_total
        this.count.review = res.data.data.review_total
        this.count.db = res.data.data.db_count
        this.dataTop5 = []
        Object.keys(res.data.data.fm_db).forEach(k => {
          this.dataTop5.push({DataBase: k, C: res.data.data.fm_db[k]})
        })
        this.$refs.pie1.setData(res.data.data.review_count)
      }).catch(e => {
        this.bus.$emit('errorHandler', e);
      }).finally(() => {
      })
      this.formatDate()
    }
  }
</script>
